<template>
	<div class="pbl">
		<div class="pb">
			<div class="item" id="pb1"></div>
			<div class="item" id="pb2"></div>
		</div>	
		<div class="ts">点击继续加载</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{
				DATAimg:[
					{imgUrl:''},
					{imgUrl:'https://hbimg.huabanimg.com/3818c3397d4084613b61d33c061e7f406da3afa324b2e-QsBHX2_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/d31500befa55854bd7d9d515e64fd221e9b8754cc611f-2R3yJT_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/524ee19f1880ec604344807fbe378a674ba0b26e3227d-7CjcOe_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/8bf324410c973fae6d6a444bdbac1d94d3f1490c1fa215-vmZE9j_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/17a31b12698f57e9956aa238ce4b89e228cb8f7c3158c-BzfAV2_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/9c874b56380624d4745e803e0035d328152c2ec564ba3-1VlO0P_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/50eb507b8a73b9323ca3ba76f3dc301995b721606328e-ffKWFs_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/5a651d6bb44bb19bf9e35c0dfeabb90267f2fe6aedcea-4u8UND_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/8177cdc7a5171ad1745054c01281e32882df9d9e43ec52-L5aXS0_fw658'},
					{imgUrl:'https://hbimg.huabanimg.com/2eaedda5135a8c318adf12111c244840f1e29f1247a084-QnZaFn_fw658'},
				]
			}
		},
		mounted(){

			let imgId = 0;
			let that = this;
			let ts = document.querySelector('.ts');
			let b =[];

			this.DATAimg.forEach(item =>{
				b.push(item.imgUrl)
			})

			function mindiv() {
				let pb1 = document.getElementById('pb1');
				let pb2 = document.getElementById('pb2');

				let pbimgs1 = pb1.children;
				let pbimgs2 = pb2.children;

				let pb1height = jisuan(pbimgs1);
				let pb2height = jisuan(pbimgs2);

				let minheight = Math.min(pb1height, pb2height);

				if (minheight == pb1height) {
					return pb1;
				}
				if (minheight == pb2height) {
					return pb2;
				}
			}

			function jisuan(pbimgs) {
				if (pbimgs == null || pbimgs.length == 0) {
					return 0
				}else{
					let height = 0;
					for (let i = 0; i < pbimgs.length; i++){
						height += pbimgs[i].height;
					}
					return height;

				}
			}

			function insertImg() {
				let inter = setInterval( function() {
					imgId++;
					if (imgId > 9) {
						clearInterval(inter);
						ts.style.display = 'block';
					}
					/*if (ts.onclick()) {
						imgId = 0
					}*/
					let img = document.createElement('img');
					img.setAttribute("src",b[imgId]);
					
					mindiv().appendChild(img);
				},100)
			}

			insertImg();

			ts.onclick = function(){
				imgId = 0
				insertImg();
			}


		}
	}
</script>

<style lang="scss">
	.pb{
		display: flex;
		flex-direction: row;
		.item{
			flex:1;
			margin:0 .1rem;
			img{
				width: 100%;
				height: auto;
				margin: .1rem 0;
			}
		}
	}
	.ts{
		width: 100%;
		height: .8rem;
		line-height: .8rem;
		text-align: center;
		background: green;
		position: absolute;
		left: 0;
		color:white;
		display: none;
	}
</style>